import { Component, Event, EventEmitter, Host, Prop, h } from '@stencil/core';
import { ICON_BUTTON_SIZES, ICON_BUTTON_VARIANTS } from '../icon-button/exports';
import { ICON_SIZES } from '../icons/exports';

@Component({
  tag: 'mdx-modal',
  styleUrl: 'modal.scss',
  shadow: true,
})
export class Modal {
  @Prop()
  open = false;

  @Prop()
  heading = '';

  @Prop()
  closeIconAriaLabel = 'Close';

  @Event()
  close: EventEmitter;

  render() {
    return this.open ? (
      <Host>
        <div class="modal">
          <mdx-icon-button
            class="modal__close"
            ariaLabel={this.closeIconAriaLabel}
            variant={ICON_BUTTON_VARIANTS.QUATERNARY}
            size={ICON_BUTTON_SIZES.MD}
            onClick={() => {
              this.open = false;
              this.close.emit();
            }}
          >
            <mdx-icon-x size={ICON_SIZES.SM} />
          </mdx-icon-button>
          <slot name="image" />
          <div class="modal__content">
            <div class="modal__heading">{this.heading}</div>
            <div class="modal__text">
              <slot name="content"></slot>
            </div>
            <div class="modal__button-bar">
              <mdx-button-group>
                <slot name="button"></slot>
              </mdx-button-group>
            </div>
          </div>
        </div>
        <div
          class="modal__backdrop"
          onClick={() => {
            this.open = false;
            this.close.emit();
          }}
        />
      </Host>
    ) : null;
  }
}
