:host .modal {
  box-sizing: border-box;
  position: fixed;
  left: 50%;
  top: 50%;
  max-height: 100vh;
  overflow-y: auto;
  transform: translate(-50%, -50%);
  box-shadow: var(--mdx-comp-modal-shadow-default);
  background-color: var(--mdx-comp-modal-background-color-default);
  border-radius: var(--mdx-comp-modal-border-radius-default);
  color: var(--mdx-comp-modal-color-default);
  animation: visible var(--mdx-comp-modal-transition-fade-in-out-duration)
    var(--mdx-comp-modal-transition-fade-in-out-timing-function) var(--mdx-comp-modal-transition-fade-in-out-delay);
  z-index: 1001;

  @media only screen and (max-width: 768px) {
    position: fixed;
    width: 100%;
    max-height: 100vh;
    max-height: -webkit-fill-available;
    border-radius: var(--mdx-comp-modal-border-radius-default) var(--mdx-comp-modal-border-radius-default) 0 0;
    bottom: 0;
    top: initial;
    transform: translate(-50%, 0%);
    animation: slide-in var(--mdx-comp-modal-transition-fade-in-out-duration)
      var(--mdx-comp-modal-transition-fade-in-out-timing-function) var(--mdx-comp-modal-transition-fade-in-out-delay);
  }

  ::slotted([slot='image']) {
    border-top-left-radius: var(--mdx-comp-modal-border-radius-default);
    border-top-right-radius: var(--mdx-comp-modal-border-radius-default);
    width: 100%;
    max-height: var(--mdx-comp-modal-image-sizing-max-height-default);
    padding: var(--mdx-comp-modal-image-padding-default);
    display: block;
  }

  &__content {
    padding: var(--mdx-comp-modal-content-spacing-padding-default);
  }

  &__heading {
    font: var(--mdx-comp-modal-font-title);
  }

  &__text {
    margin-top: var(--mdx-comp-modal-content-gap-title-text);
    font: var(--mdx-comp-modal-font-paragraph);
  }

  &__button-bar {
    margin-top: var(--mdx-comp-modal-content-gap-content-button-group);
    display: flex;
    justify-content: flex-end;
    gap: var(--mdx-comp-button-group-horizontal-margin-gap-between-default);

    @media only screen and (max-width: 768px) {
      flex-direction: column;
    }
  }

  &__close {
    position: absolute;
    right: 0px;
    top: 0px;
  }

  &__backdrop {
    background-color: rgba(0, 0, 0, 0.3);
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 1000;
  }
}

@keyframes visible {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes slide-in {
  from {
    opacity: 0;
    transform: translate(-50%, 100%);
  }
  to {
    opacity: 1;
    transform: translate(-50%, 0%);
  }
}
